import fetch from 'node-fetch';

import process from 'child_process';

import test from 'ava';

test.beforeEach(async (test) => {
  const devCommand = process.spawn('yarn', ['run', 'dev']);

  const devPromise = new Promise((resolve, reject) => {
    devCommand.stdout.on('data', (data) => {
      resolve(data);
    });
    devCommand.stderr.on('data', (data) => {
      reject(data);
    });
  });
  await devPromise;
});

test('PATH:/greeting should get ', async (test) => {
  const resultPromise = fetch('http://localhost:3000/greeting');
  const result = await resultpromise;
  console.log('++++++++');
  console.log(result);
  console.log('++++++++');
});
